* {
  box-sizing : border-box;
  margin : 0;
  padding : 0;
  background-color: rgb(243, 239, 239);
}

#header{
    color: rgb(66, 173, 255);
    font-size: 2rem;
    margin: 4rem;
    text-align: center;

}
*{
    margin: 0.8rem;
}
.inputWrapper{
    margin: auto;
    text-align: center;
}

.select{
    width: 6rem;
    height: 2rem;
    border: 1px solid grey;
    font-weight: bold;
}

.button{
    width: 6rem;
    height: 2rem;
    border: 1px solid grey;
    font-weight: bold;
}
#wrapper{
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    

}
.income{
    margin: 1rem;
    width: 28rem;
    border: 3px solid rgb(142, 223, 142);
    
}
.expense{
    margin:1rem;
    width: 28rem;
    border: 3px solid rgb(176, 81, 81);
}
.total{
    margin:auto;
    width: 30rem;
    text-align: center;
    background-color: rgb(250, 200, 60);
    border-radius: 1rem 1rem;
}
.title-income{
    background-color:rgb(142, 223, 142);
    padding:1rem;
    font-size: 1.5rem;
    font-weight: bolder;
}

.title-expense{
    background-color: rgb(176, 81, 81);
    padding:1rem;
    font-size: 1.5rem;
    font-weight: bolder;
}

.datadisplay{
    list-style-type: none;
    padding: 0.5rem;
}

.sum{
    font-size: 2rem;
}
.total{
    margin-top:1rem;
    font-size: 1.5rem;
    font-weight: bolder;
    padding: 1rem;
}
.FormFieldwrapper{
  width: 45%;
  margin: 6rem auto;
  border: 1px solid #eee;
  border-radius : 8px;
  box-shadow : 0 2px 3px #ccc;
  padding : 2rem;
  background-color : #eefeee;
}


